import Vue from 'vue'
import VueRouter from 'vue-router'
import Cadastro from '../views/cadastroBanco.vue'
import Login from '../views/loginBanco.vue'
import Conta from '../views/contaBanco.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/login' // Redirecionar para a página de login por padrão
  },
  {
    path: '/cadastroBanco',
    name: 'Cadastro',
    component: Cadastro
  },
  {
    path: '/loginBanco',
    name: 'Login',
    component: Login
  },
  {
    path: '/contaBanco',
    name: 'Conta',
    component: Conta
  }
]

const router = new VueRouter({
  routes
})

export default router
